.navbar {
  background-color: #2f3542;
}

body,
.note-list .list-group-item {
  background-color: #2f3542;
}
.note-list {
  padding-bottom: 80px;
}
#note-list--search {
  width: calc(100vw - 140px);
  color: #57606f;
  border: 1px solid #57606f;
  border-radius: 40px;
  padding: 0.5em 1em;
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}
#note-list--search:focus {
  outline: none;
  border: 1px solid #dfe4ea;
  color: #dfe4ea;
  box-shadow: none;
}
.logo {
  position: absolute;
  top: 24px;
  right: 20px;
}

.add_note_btn {
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  padding: 2rem;
  background-color: #2ed573 !important;
  z-index: 9;
}
.add_note_btn .fa {
  position: absolute;
  font-size: 1.4rem;
  top: 50%;
  left: 50%;
  -webkit-transform: translateY(-50%) translateX(-50%);
          transform: translateY(-50%) translateX(-50%);
}

.list-group-item {
  border: none;
  border-bottom: 1px solid #57606f;
  margin-bottom: 0;
}
.list-group-item:last-of-type {
  border-bottom: none;
}
.list-group-item p {
  color: #a4b0be;
}
.list-group-item a {
  color: #ced6e0;
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}
.list-group-item a:hover h3 {
  color: #fff;
}

.note-editor {
  background-color: #2f3542;
  color: #dfe4ea;
  padding: 56px 0;
}

.ql-toolbar.ql-snow,
.ql-container.ql-snow {
  border: none;
}

.note-editor_nav {
  position: fixed;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  top: 0;
  left: 0;
  width: 100vw;
  height: 56px;
  background-color: #2f3542;
  z-index: 9;
}
.note-editor_button {
  position: fixed;
  height: 24px;
  width: 24px;
  left: 16px;
}

.icon-x {
  color: #57606f;
  font-size: 22px;
}

.icon-x:hover {
  color: #ff6b81;
  cursor: pointer;
}
.note-editor_nav input {
  text-align: center;
}
.note-editor_nav input:focus {
  outline: none;
}

.bottom-bar {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  position: fixed;
  bottom: 0;
  left: 0;
  height: 56px;
  width: 100vw;
  background-color: #2f3542;
}
.bottom-bar:before {
  display: block;
  position: absolute;
  content: "";
  height: 1px;
  width: 66vw;
  background: #57606f;
}
.bottom-bar_button_container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.bottom-bar_button {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 32px;
  width: 32px;
  margin: 0 6px;
  color: #57606f;
  font-size: 22px;
}
.bottom-bar_button:hover {
  cursor: pointer;
}
.icon-save:hover {
  color: #2ed573;
}
.icon-favorite:hover {
  color: #ffa502;
}
.icon-delete:hover {
  color: #ff6b81;
}
.is-favorite {
  color: #ffa502;
}

